/* Breakpoint Definitions */
.gallery__container {
  grid-area: gallery;
  margin: 16px; }
  .gallery__container picture img {
    width: 100%;
    height: 100%;
    object-fit: cover; }

.gallery__row {
  display: grid;
  grid-template-columns: repeat(12, 6vw);
  grid-gap: 8px 8px;
  margin-bottom: 8px; }

.image-0 {
  grid-area: one; }

.image-1 {
  grid-area: two; }

.image-2 {
  grid-area: three; }

.portrait-portrait {
  grid-template-rows: repeat(9, 6vw);
  grid-template-areas: "one one one one one one two two two two two two" "one one one one one one two two two two two two" "one one one one one one two two two two two two" "one one one one one one two two two two two two" "one one one one one one two two two two two two" "one one one one one one two two two two two two"; }

.landscape {
  grid-template-rows: repeat(8, 6vw);
  grid-template-areas: "one one one one one one one one one one one one" "one one one one one one one one one one one one" "one one one one one one one one one one one one" "one one one one one one one one one one one one" "one one one one one one one one one one one one" "one one one one one one one one one one one one" "one one one one one one one one one one one one" "one one one one one one one one one one one one"; }

.landscape-portrait {
  grid-template-rows: repeat(6, 6vw);
  grid-template-areas: "one one one one one one one one two two two two" "one one one one one one one one two two two two" "one one one one one one one one two two two two" "one one one one one one one one two two two two" "one one one one one one one one two two two two" "one one one one one one one one two two two two"; }

