@import url("https://fonts.googleapis.com/css?family=Raleway:200,400|Work+Sans:200,400");
@import url("https://use.typekit.net/fha1rpn.css");
body {
  color: #444;
  font-size: 16px;
  font-family: "ff-meta-serif-web-pro", "serif";
  letter-spacing: 0.16px;
  text-rendering: optimizeLegibility;
  -webkit-font-smoothing: subpixel-antialiased;
  background-color: #fefefe;
  margin: 0px;
  padding: 0px; }

h1, h2, h3, h4, h5 {
  font-family: "brandon-grotesque", "sans-serif";
  font-weight: 400;
  padding: 0px;
  margin: 0px; }

h2 {
  font-size: 35px;
  letter-spacing: -0.35px;
  line-height: 35px;
  margin-block-end: 29.05px; }

h3 {
  font-size: 16px;
  text-transform: uppercase;
  letter-spacing: 2.4px;
  line-height: 25.6px;
  margin-top: 15px;
  margin-block-end: 29.05px;
  margin-block-start: 29.05px; }

p {
  line-height: 27.2px;
  font-weight: 500; }

a {
  color: #2d818f;
  text-decoration: none;
  padding-bottom: .05em;
  border-bottom-width: 1px;
  border-bottom-style: solid;
  border-bottom-color: #2d818f;
  transition: border-color .15s ease-out, color .15s ease-out; }

a:hover {
  color: rgba(45, 129, 143, 0.6);
  border-color: rgba(45, 129, 143, 0.6); }

.content {
  display: grid;
  justify-content: center; }
  .content .content__copy {
    margin: 0 auto; }
  .content .content__copy > * {
    margin: 16px; }
  .content .content__callout {
    text-align: center; }
  .content .content__gallery {
    width: 100vw;
    display: grid;
    grid-template-columns: 4fr 12fr 4fr;
    grid-template-rows: auto;
    grid-template-areas: ". gallery ."; }

.padding__high {
  padding-top: 100px;
  padding-bottom: 100px; }

.padding__medium {
  padding-top: 60px;
  padding-bottom: 60px; }

.padding__low {
  padding-top: 30px;
  padding-bottom: 30px; }

.width__wide {
  width: 96vw; }

.width__medium {
  width: 96vw; }
  @media all and (min-width: 960px) {
    .width__medium {
      width: 80vw; } }

.width__narrow {
  width: 96vw; }
  @media all and (min-width: 960px) {
    .width__narrow {
      width: 60vw; } }
  @media all and (min-width: 1024px) {
    .width__narrow {
      width: 70vw; } }

.header {
  display: grid;
  grid-template-columns: 80px auto;
  grid-template-areas: "navigation logo";
  align-items: center;
  justify-items: center;
  height: 60px;
  width: 100vw;
  background-color: #fafafa; }
  @media all and (min-width: 960px) {
    .header {
      height: 80px;
      grid-template-columns: 300px auto;
      grid-template-areas: "logo navigation"; } }

.logo {
  grid-area: logo;
  justify-self: start;
  padding-left: 16px;
  cursor: pointer; }
  @media all and (min-width: 960px) {
    .logo {
      padding-left: 32px; } }

.logo h1 {
  font-family: "Work Sans", "sans-serif";
  font-size: 17px;
  text-transform: uppercase;
  font-weight: 400;
  letter-spacing: 4.25px; }
