/* Typography */
/* Breakpoint Definitions */
.titlecard {
  width: 90vw;
  text-align: center; }
  .titlecard h1 {
    color: rgba(28, 28, 28, 0.6);
    font-weight: 300;
    text-align: center;
    text-transform: uppercase;
    font-size: 46px;
    letter-spacing: 8px;
    margin-bottom: 16px; }
    @media all and (min-width: 960px) {
      .titlecard h1 {
        font-size: 60px;
        letter-spacing: 11px; } }

.titlecard__image {
  width: 100%;
  height: 300px;
  background-size: contain;
  background-position: center center;
  background-repeat: no-repeat; }
  @media all and (min-width: 768px) {
    .titlecard__image {
      height: 400px; } }
  @media all and (min-width: 960px) {
    .titlecard__image {
      height: 600px; } }
  @media all and (min-width: 1024px) {
    .titlecard__image {
      height: 800px; } }
